import React, { useEffect, useState } from 'react'
import emailjs from 'emailjs-com';
import '../Styles/Contact.css'
import { Col, Container, Row } from 'react-bootstrap';
import Aos from 'aos'

function Contact() {
    const [send, setSend] = useState(false);
 

    useEffect(() => {
        Aos.init({
            duration : 2000
          });
    }, [])

    function sendEmail(e) {
        e.preventDefault();
    
        emailjs.sendForm('service_ty0uhgb', 'template_s4puioh', e.target,'user_TsYO03RUrOqMxYewfyPt4')
          .then((result) => {
              console.log(result.text);
          }, (error) => {
              console.log(error.text);
          });
      }

      const submitHandle=()=>{
          setSend(true);
      }
    return (
        <div className="Contact">
 
            <Container fluid >
                <Row className="Main-Row">
                    <Col className="main-col">
                    <div data-aos="fade-up">
                        <div className="background">
                            <h1>Napisz do mnie!</h1>
                            <form className="contact-form" onSubmit={sendEmail}>
                                <input type="hidden" name="contact_number" />
                                <label>Name</label>
                                <input type="text" name="user_name" placeholder="Podaj imię"/>
                                <label>Email</label>
                                <input type="email" name="user_email" placeholder="Zostaw swój email, na pewno sie odezwę!"/>
                                <label>Message</label>
                                <textarea name="message" />
                                <div  className="submit">
                                <input type="submit" value="Send"  onClick={submitHandle}/>
                                </div>
                                {send? <h2>Dziękuję za wiadomość!</h2> : ''}
                            </form>
                        </div>
                    </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="space"></div>
                    </Col>
                </Row>
            </Container>
          
        </div>
    )
}

export default Contact

