import React, { useEffect } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import '../Styles/AboutMe.css'
import Image from 'react-bootstrap/Image'
import AOS from 'aos'
function AboutMe() {

    useEffect(() => {
        AOS.init({
            duration : 1000
          });
    }, [])
    return (
        <div className="AboutMe">
            <Container>
                <Row className="Main-Row">
                    <Col lg>
                        <div data-aos="fade-up-right">
                            <Image id="img" src="me.jpg" fluid rounded/>
                        </div> 
                    </Col>
                    <Col lg className="text-column">
                        <h2>O mnie</h2>
                        <p>Jestem studentem III roku stacjonarnych studiów pierwszego stopnia na kierunku
                            Informatyka, specjalność: Aplikacje internetowe i bazy danych na Uniwersytecie
                            Gdańskim. Programując uwielbiam rozwiązywać realne problemy oraz tworzyć użyteczne i przydatne rzeczy. Poza informatyką interesuję się sportem. Od 11 lat trenuje piłkę ręczną. Kocham stawiać sobie wyzwania.
                        </p>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default AboutMe
