import Carousel from 'react-bootstrap/Carousel'
import React, { useState } from 'react'
import "../Styles/Slider.css"
import { Image } from 'react-bootstrap';

function Slider(props) {

    return (
        <Carousel className="Slider">
        <Carousel.Item>
          <Image  className="img-slider" src={props.url1}></Image>
        </Carousel.Item>
        { props.url2? 
        <Carousel.Item>
        <Image className="img-slider" src={props.url2}></Image>
        </Carousel.Item> : ''}
        { props.url3 ? 
        <Carousel.Item>
        <Image className="img-slider" src={props.url3}></Image>
        </Carousel.Item> : ''}
      </Carousel>
    )
}

export default Slider
