import React from 'react'
import { Col, Container, Image, Row } from 'react-bootstrap'
import Slider from '../SmallComponents/Slider'
import "../Styles/Projects.css"

function Projects() {
    return (
        <div className="Projects">
           <Container fluid>
               <Row>
                   <Col>
                        <div className="Header">
                                <h1>Projekty</h1>
                        </div>
                   </Col>
               </Row>
               <Row className="Main-Row-Projects">
                   <Col lg>
                       <h2>Organizer spotkań sportowych</h2>
                        <div className="project-div">
                            <Slider
                            url1="https://raw.githubusercontent.com/pawel25pitucha/JavaFxProjekt/master/Screenshots/screen1.png"
                            url2="https://raw.githubusercontent.com/pawel25pitucha/JavaFxProjekt/master/Screenshots/screen7.png"
                            url3="https://raw.githubusercontent.com/pawel25pitucha/JavaFxProjekt/master/Screenshots/screen2.png"/>
                            <div className="project-caption">
                                <a href="https://github.com/pawel25pitucha/JavaFxProjekt" target="_blank"><button>Zobacz Projekt</button></a>
                                <p>Ogranizer spotkań sportowych to prosta okienkowa aplikacja typu CRUD napisana w JavaFx. Był to mój projekt na uczelnie.</p>
                                <p className="technology"> Użyte technologie to Java, JavaFx, Sql Server...</p>
                            </div>
                        </div>
                   </Col>
                   <Col lg>
                        <h2>MessengerApp</h2>
                        <div className="project-div">
                            <Slider
                            url1="https://raw.githubusercontent.com/pawel25pitucha/MessengerApp/master/Screenshot_3.png"
                            url2="https://raw.githubusercontent.com/pawel25pitucha/MessengerApp/master/Screenshot_1.png"
                            />
                             <div className="project-caption">
                                <a href="https://github.com/pawel25pitucha/MessengerApp" target="_blank"><button>Zobacz Projekt</button></a>
                                <p>MessengerApp to aplikacja webowa symulująca popularne komunikatory. Umożliwia pisanie pomiędzy użytkownikami, tworzenie pokoi oraz autoryzacje poprzez logowanie na konto google. Aplikacja napisana w React z bazą danych utworzoną w Firebase. Hosting komunikatora również poprzez Firebase.</p>
                                <p className="technology">Użyte technologie: React, React-Router, React-Hooks, React-Redux, Firebase...</p>
                            </div>
                        </div>
                   </Col>
                   <Col lg>
                        <h2>Blog</h2>
                        <div className="project-div">
                            <Slider 
                            url1="https://raw.githubusercontent.com/pawel25pitucha/BlogSpringFrontend/main/Screenshot_4.png"
                            url2="https://raw.githubusercontent.com/pawel25pitucha/BlogSpringFrontend/main/Screenshot_1.png"
                            url3="https://raw.githubusercontent.com/pawel25pitucha/BlogSpringFrontend/main/Screenshot_2.png"
                            />
                             <div className="project-caption">
                             <a href="https://github.com/pawel25pitucha/BlogSpringFrontend" target="_blank"><button>Zobacz Projekt</button></a>
                                <p>Blog to aplikacja webowa napisana w React. Jest to Front do stworzonego na potrzeby studiów API w Java Spring, które można zobaczyć na moim Githubie. Aplikacja umożliwia dodawanie/usuwanie/edytowanie postów oraz komentarzy,wyszukiwanie postów po tagach, wyświetlanie statystyk poszczególnych autorów. </p>
                                <p className="technology">Użyte technologie: React, React-Bootstrap, Material-UI, React-Router...</p>
                            </div>
                        </div>
                   </Col>
               </Row>
               <Row>
                   <Col>
                        <div className="github">
                            <div className="gridGithub">
                            <Image fluid className="githublogo" src="https://github.githubassets.com/images/modules/logos_page/Octocat.png"></Image>
                            <h3><a href="https://github.com/pawel25pitucha" target="_blank">GitHub</a></h3>
                            </div>
                            
                        </div>
                   </Col>
                   <Col>
                        <div className="github">
                             <div className="gridGithub">
                                <Image id="img-cv" className="githublogo" src="https://aux.iconspalace.com/uploads/download-icon-256-361231194.png"></Image>
                                <h3><a download href="Cv-Pitucha.pdf">Pobierz moje CV </a></h3>
                            </div>                        
                        </div>
                   </Col>
               </Row>
  
           </Container>
        </div>
    )
}

export default Projects
