import React, { useEffect, useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import '../Styles/WelcomePage.css'
import Aos from 'aos'
import {Link} from 'react-scroll'


function WelcomePage() {
    useEffect(() => {
        Aos.init({
            duration : 3000
          });
    }, [])
 
    return (
       
        <div  className="WelcomePage">
            <Container fluid>
                <Row>
                    <Col className="Name">
                        <div></div>
                        <div className="name-div">
                            <h1 className="name-h1">Paweł Pitucha</h1>
                            <h2 className="name-h2">Frontend Developer</h2>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col lg className="Column">
  
                         <a><Link to="skills" activeClass="active" spy={true}>Skills</Link></a>

                    </Col>
                    <Col lg className="Column">

                    <a><Link to="Projects" activeClass="active" spy={true}>Projects</Link></a>

                    </Col>
                    <Col lg className="Column">

                        <a><Link to="Contact" activeClass="active" spy={true}>Contact</Link></a>
     
                    </Col>
                </Row>
                <Row className="Scroll-Row">
                    <Col className="Scroll-Column">
                        <div></div>
                        <div className="scroll-div">
                            <Link to="skills" activeClass="active" spy={true}> <a href="#" class="scroll-down" address="true"></a></Link>
                        </div> 
                    </Col>
                </Row>
            </Container>
        </div>
     

    )
}

export default WelcomePage
